<script lang="ts">
  import { WeekDays } from "../models/DayModel";

  export let value: Array<number> = [];

  const days = WeekDays;

  const toggleDay = day => {
    const isInArray = value.includes(day.id);

    if (isInArray) {
      value = value.filter(v => v !== day.id);
    } else {
      value = [...value, day.id];
    }
  };
</script>

<style lang="scss">
  @import "styles/variables";
  @import "styles/colors";
  main {
    display: flex;
    justify-content: space-between;

    .day {
      border-radius: 50%;
      font-weight: 700;
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;

      &:active,
      &.selected {
        background-color: $darkestGray;
      }
    }
  }
</style>

<main>
  {#each days as day (day.id)}
    <div
      class="day"
      class:selected={value.includes(day.id)}
      on:click={() => toggleDay(day)}>
      {day.symbol}
    </div>
  {/each}
</main>
